@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Archivo:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Karla:wght@200&display=swap);
:root {
  --bg-light: #def1ff;
  --color-light: #002d50;
  --color-alert: #090043;
  --color-alert-hover: #271867;
}

html,
body {
  padding: 0;
  margin: 0;
  font-size:16px;
  line-height: 1.6;
  background-color: #def1ff !important;
  background-color: var(--bg-light) !important;
  color: #002d50 !important;
  color: var(--color-light) !important;
  font-family: 'Archivo', "Quicksand", sans-serif;
  font-style: normal;
  scroll-behavior: smooth; 
  overflow-x: hidden;
  /* overflow-y: auto; */
}

pre, textarea {
  color: #def1ff !important;
  color: var(--bg-light) !important;
}

@media Screen and (max-width: 768px){
  html {
    font-size:12px !important;
  }
}

* {
  box-sizing: border-box;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  color: #090043;
  color: var(--color-alert);
  text-decoration: none;
}                                                                            

a:hover {
  color: #271867;
  color: var(--color-alert-hover);
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

/* pre > code {
  color: var(--color-alert);
  text-decoration: none;
} */

/* SCROLLBAR */

* {
  scrollbar-color: #002d50;
  scrollbar-color: var(--color-light);
  scrollbar-width: thin;
}

*::-webkit-scrollbar-track
{
background-color: #002d50;
background-color: var(--color-light);
}

*::-webkit-scrollbar
{
width: 10px;
background-color: #002d50;
background-color: var(--color-light);
}

*::-webkit-scrollbar-thumb
{
background-color:  #FFF;
}
