  max-height: 21.875rem;
  overflow: auto;
}

div[data-id='error-container'] {
  position: absolute;
  background: tomato;
  color: #19154f;
  left: 0.5%;
  bottom: 2px;
  width: 99%;
  padding: 0px 15px 0px 15px;
}

div[data-id='error-container'] * {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

div[data-id='controls-container'] {
  width:85%;
  position: absolute;
  top: 10px;
  left: 10px;
  z-index: 10;
}

div[data-id='controls-container'] button {
  margin: 2px;
  cursor: pointer;
  padding: 5px;
  border-radius: 5px;
  background-color: #0271f8;
  color: #fff;
}

div[data-id='controls-container'] button:hover {
  -webkit-transform: scale(1.08);
          transform: scale(1.08);
}

div[data-id='controls-container'] button:active {
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}

div[data-id='controls-container'] button:focus {
  outline: none;
}

div[data-id='controls-container'] {
